import React, { Component } from 'react';
import './App.css';
import ShowEnglish from './ShowEnglish'
import SubmitEnglishInfo from './SubmitEnglishInfo'

var NebPay = require("./dist/nebpay");
var nebPay = new NebPay();

var Nebulas = require('nebulas')
var Neb = Nebulas.Neb; // Neb
var neb = new Neb(new Nebulas.HttpRequest("https://mainnet.nebulas.io"));
var Account = Nebulas.Account;
var api = neb.api;
// mainnet
// n21uXb2CvgzdwhnKzyfCUHioMRnZJCHPTaK
const dappAddress = "n21uXb2CvgzdwhnKzyfCUHioMRnZJCHPTaK";
// testnet
// const dappAddress = "n1zBWaitSk7oY2jpyidGyeAfkdzpXqa1yjj";


class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      englishInfos: []
    }
  }

  submitEnglishInfoToBLC = (englishWord,timeString) => {
    console.log("submitEnglishInfoToBLC")
    console.log(englishWord)
    console.log(timeString)

    console.log("调用合约的save方法!")
    var value = "0";
    var callFunction = "save"
    var callArgs = "[\"" + englishWord + "\",\"" + timeString + "\"]"
    console.log(callArgs)
    nebPay.call(dappAddress, value, callFunction, callArgs, {    //使用nebpay的call接口去调用合约,
        listener: this.cbPush        //设置listener, 处理交易返回信息
    });
  }

  cbPush = (resp) => {
      console.log("response of push: " + JSON.stringify(resp))
      var intervalQuery = setInterval(() => {
        api.getTransactionReceipt({hash: resp["txhash"]}).then((receipt) => {
            console.log("判断数据提交到区块链的状态")
            // console.log(receipt)
            if (receipt["status"] === 2) {
                console.log("pending.....")
            } else if (receipt["status"] === 1){
                console.log("交易成功......")
                //清除定时器
                clearInterval(intervalQuery)
                var from = "n1YLc2ndCuzS5hKthxvpBDtv1c1YPa6Pacw"
                var value = "0";
                var nonce = "0"
                var gas_price = "1000000"
                var gas_limit = "2000000"
                var callFunction = "get";
                var callArgs = "[\"\"]"; //in the form of ["args"]
                var contract = {
                    "function": callFunction,
                    "args": callArgs
                }

                neb.api.call(from,dappAddress,value,nonce,gas_price,gas_limit,contract).then((resp) => {

                  console.log(resp)
                  console.log(resp["result"])
                  var englishInfos = JSON.parse(resp["result"])
                  console.log(englishInfos)
                  this.setState({englishInfos})
                })
            }else {
                console.log("交易失败......")
                //清除定时器
                clearInterval(intervalQuery)
            }
        });
      }, 5000);

  }

  componentWillMount() {
    console.log("componentWillMount")
    this.setState({isShowInputView: false})
    var from = "n1YLc2ndCuzS5hKthxvpBDtv1c1YPa6Pacw"
    var value = "0";
    var nonce = "0"
    var gas_price = "1000000"
    var gas_limit = "2000000"
    var callFunction = "get";
    var callArgs = "[\"\"]"; //in the form of ["args"]
    var contract = {
        "function": callFunction,
        "args": callArgs
    }

    neb.api.call(from,dappAddress,value,nonce,gas_price,gas_limit,contract).then((resp) => {

      console.log(resp)
      console.log(resp["result"])
      var englishInfos = JSON.parse(resp["result"])
      console.log(englishInfos)
      this.setState({englishInfos})
    })
  }

  render() {
    return (
      <div className="App">
        <div className="HeaderView">
          <div style={{width: '70%',backgroundColor:'green',fontSize: 60,color: 'white',textAlign: 'center'}}>全球最大的星云链唯美英语社区</div>
          <div style={{width: '30%',fontSize: 60,color: 'white',backgroundColor: 'rgb(244,76,9,0.3)',textAlign: 'center'}}>作者：黎跃春</div>
        </div>
        <SubmitEnglishInfo submitEnglishInfoToBLC={this.submitEnglishInfoToBLC}/>
        {
          this.state.englishInfos.length > 0 ? this.state.englishInfos.map((info) => {
            return (
              <ShowEnglish englishWord={info.englishWord} timeString={info.timeString} author={info.author}/>
            )
          }) : <div />
        }
      </div>
    );
  }
}

export default App;



