import React, { Component } from 'react';


class ShowEnglish extends Component{
  constructor(props) {
    super(props)
  }


  static defaultProps = {
    englishWord: "",
    timeString: "",
    author: ""
  }

  render() {

    return (
      <div style={{display: 'flex',flexDirection: 'column',alignItems: 'center',width: '60%',marginTop: 20,borderWidth: 5,borderStyle: 'dashed',borderColor: 'green',padding: 10}}>
        <div style={{width: '100%',fontSize: 30,textAlign: 'left',padding: 10,fontWeight: 'bold'}}>{this.props.englishWord}</div>
        <div style={{height: 1,backgroundColor: 'white',width: '100%'}}/>
        <div style={{width: '100%',textAlign: 'right',padding: 10}}>Time：{this.props.timeString}</div>
        <div style={{width: '100%',textAlign: 'right',padding: 10}}>Author：{this.props.author}</div>
      </div>
    )
  }
}

export default ShowEnglish;



