import React, { Component } from 'react';


Date.prototype.Format = function (fmt) { //author: meizz
    var o = {
        "M+": this.getMonth() + 1, //月份
        "d+": this.getDate(), //日
        "h+": this.getHours(), //小时
        "m+": this.getMinutes(), //分
        "s+": this.getSeconds(), //秒
        "q+": Math.floor((this.getMonth() + 3) / 3), //季度
        "S": this.getMilliseconds() //毫秒
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}

class SubmitEnglishInfo extends Component{
  constructor(props) {
    super(props)
  }

  static defaultProps = {
    submitEnglishInfoToBLC: (enlishWord,timeString) => {}
  }

  render() {
    return (
      <div style={{width: '70%',marginTop: 10,display: 'flex',borderWidth: 5,borderColor: 'green',borderStyle: 'dashed'}}>
        <textarea
          style={{flex: 4}}
          name="textarea"
          rows="10"
          id="textarea_id"
          cols="50"
          placeholder="请输入唯美英语中英文金句..."
          ></textarea>
        <button
          style={{flex: 1,fontWeight: 'bold',fontSize: 25}}
          onClick={() => {
            var textValue = document.getElementById("textarea_id").value;
            var dateString = new Date().Format("yyyy-MM-dd hh:mm:ss");
            this.props.submitEnglishInfoToBLC(textValue,dateString)
          }}
        >提交到星云链</button>
      </div>
    )
  }

}

export default SubmitEnglishInfo;



